"use client"
import React, { useState } from 'react'
import { useRouter } from 'next/navigation'
import API from '@/lib/api'

export default function CreateGroupPage() {
  const router = useRouter()
  const [name, setName] = useState('')
  const [description, setDescription] = useState('')
  const [city, setCity] = useState('')
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const submit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError(null)
    const token = localStorage.getItem('token')
    if (!token) {
      setError('Please login first')
      setLoading(false)
      return
    }
    try {
      const res = await API.post('/groups', { name, description, city }, { headers: { Authorization: `Bearer ${token}` } })
      router.push(`/groups/${res.data.group.id}`)
    } catch (err: any) {
      setError(err?.response?.data?.error || 'Failed to create group')
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="mx-auto max-w-2xl p-6">
      <h2 className="mb-4 text-2xl font-semibold text-white">Create a Group</h2>
      <form onSubmit={submit} className="flex flex-col gap-3">
        <input value={name} onChange={(e) => setName(e.target.value)} placeholder="Group name" className="rounded border border-white/30 bg-white/10 px-3 py-2 text-white placeholder:text-white/60" required />
        <textarea value={description} onChange={(e) => setDescription(e.target.value)} placeholder="Description" className="rounded border border-white/30 bg-white/10 px-3 py-2 text-white placeholder:text-white/60" rows={4} />
        <input value={city} onChange={(e) => setCity(e.target.value)} placeholder="City" className="rounded border border-white/30 bg-white/10 px-3 py-2 text-white placeholder:text-white/60" required />
        <button disabled={loading} className="rounded bg-pink-500 px-4 py-2 text-white hover:bg-pink-600 disabled:opacity-50">
          {loading ? 'Creating...' : 'Create Group'}
        </button>
        {error && <div className="text-sm text-red-400">{error}</div>}
      </form>
    </div>
  )
}
